import Demrad from '../../assets/images/brands/demrad.jpg';
import Drazice from '../../assets/images/brands/drazice.jpg';
import Ebara from '../../assets/images/brands/Ebara.png';
import Electrolux from '../../assets/images/brands/electrolux.png';
import Ferroli from '../../assets/images/brands/ferroli.png';
import Gorenje from '../../assets/images/brands/Gorenje.png';
import Grundfos from '../../assets/images/brands/grundfoss.png';
import Hermann from '../../assets/images/brands/Hermann.png';
import Junkers from '../../assets/images/brands/junkers.jpg';
import Kospel from '../../assets/images/brands/kospel.jpg';
import Novatec from '../../assets/images/brands/novatec.jpg';
import Pedrollo from '../../assets/images/brands/pedrollo.png';
import Protherm from '../../assets/images/brands/protherm.png';
import SaunierDuval from '../../assets/images/brands/saunier-duval.png';
import Sime from '../../assets/images/brands/sime.png';
import Speroni from '../../assets/images/brands/Speroni.png';
import Termet from '../../assets/images/brands/termet.png';
import Vaillant from '../../assets/images/brands/vaillant.jpg';
import Viessmann from '../../assets/images/brands/viessmann.png';
import Watomo from '../../assets/images/brands/watomo.png';
import Wilo from '../../assets/images/brands/wilo.png';
import Immergas from '../../assets/images/brands/immergas.png';

class Layout extends Component {

  constructor(props) {
    super(props);
    this.state = {
      navigationItems: [
        { title:'Главная',
          active:true,
          id:1,
          img:homeImg,
          header:'У Вас поломка котла или водонагревателя?',
          sentence:'Или может нуждаетесь в его приобретении?',
          conclusion:'Значит Вам к нам!'
        },
        { title:'Услуги',
          active:false,
          id:2,
          sentence:'Мы предоставляем следующие виды услуг:',
          details:[
            { title:'Ремонт',
              info:'Ремонт котлов, насосов, газовых колонок и водонагревателей',
              moreInfo:`Качественный ремонт газовых котлов и водонагревателей. Наши специалисты выполняют диагностику
               и ремонт котлов, газовых колонок, насосов и водонегравателей ведущих европейских производителей.`,
              img:repair
            },
            { title:'Установка оборудования',
              info:'Установка котлов, газовых колонок, насосов и водонагревателей',
              moreInfo:`Наш персонал производит установки газовых и электрических котлов, газовых колонок, а также
                        водонагревателей, насосов и насосных станций.`,
              img:installing
            },
            { title:'Продажа запчастей и комплектующих',
              info:'Огромный выбор запчастей и комплектующих',
              moreInfo:`У нас Вы можете заказать все необходимые детали и запчасти ведущих европейских
                производителей для интересующей Вас модели.
                Звоните, и мы ответим на все интересующие Вас вопросы.`,
              img:details
            },
          ]
        },
        {
          title:'Бренды',
          active:false,
          id:3,
          sentence:'Наша команда обслуживает продукцию следующих брендов:',
          products:[
            {
              name:'Котлы',
              brands:[
                {brandName:'Viessmann', img:Viessmann},
                {brandName:'Vaillant', img:Vaillant},
                {brandName:'Ariston', img:Ariston},
                {brandName:'Demrad', img:Demrad},
                {brandName:'Chaffoteaux', img:Chaffoteaux},
                {brandName:'Bosch', img:Bosch},
                {brandName:'Termet', img:Termet},
                {brandName:'Hermann', img:Hermann},
                {brandName:'Ferroli', img:Ferroli},
                {brandName:'Junkers', img:Junkers},
                {brandName:'Gorenje', img:Gorenje},
                {brandName:'Baxi', img:Baxi},
                {brandName:'Beretta', img:Beretta},
                {brandName:'Kospel', img:Kospel},
                {brandName:'Protherm', img:Protherm},
                {brandName:'Saunier Duval', img:SaunierDuval},
                {brandName:'Sime', img:Sime},
                {brandName:'Immergas', img:Immergas}
              ]
            },
            {
              name:'Газовые колонки',
              brands:[
                {brandName:'Beretta', img:Beretta},
                {brandName:'Termet', img:Termet},
                {brandName:'Ariston', img:Ariston},
                {brandName:'Bosch', img:Bosch},
                {brandName:'Demrad', img:Demrad},
                {brandName:'Ferroli', img:Ferroli},
                {brandName:'Vaillant', img:Vaillant},
                {brandName:'Junkers', img:Junkers},
                {brandName:'Gorenje', img:Gorenje},
                {brandName:'Saunier Duval', img:SaunierDuval},
                {brandName:'Electrolux', img:Electrolux}
              ]
            },
            {
              name:'Бойлеры',
              brands:[
                {brandName:'Gorenje', img:Gorenje},
                {brandName:'Ariston', img:Ariston},
                {brandName:'Electrolux', img:Electrolux},
                {brandName:'Novatec', img:Novatec},
                {brandName:'Ferroli', img:Ferroli},
                {brandName:'Termet', img:Termet},
                {brandName:'Demrad', img:Demrad},
                {brandName:'Drazice', img:Drazice},
                {brandName:'Kospel', img:Kospel},
                {brandName:'Vaillant', img:Vaillant}
              ]
            },
            {
              name:'Насосы',
              brands:[
                {brandName:'Watomo', img:Watomo},
                {brandName:'Speroni', img:Speroni},
                {brandName:'Ebara', img:Ebara},
                {brandName:'Grundfos', img:Grundfos},
                {brandName:'Wilo', img:Wilo},
                {brandName:'Pedrollo', img:Pedrollo}
              ]
            }
        ]
        },
        { title:'Отзывы',
          id:4,
          active:false,
          img:testimonialsImg,
          testimonials:[
            { author: 'Виталий Владимирович',
              date: '18/06/2017',
              stars: 5,
              review: `Спасибо ребятам - котел почищен быстро! Работа выполнена настоящими профессионалами!`
            },
            { author: 'Игорь',
              date: '21/08/2017',
              stars: 5,
              review: `Спасибо за ремонт газового котла Ferroli.
                Сделали все в этот же день. Видно, что работают профессионалы. Цена адекватная.`
            },
            { author: 'Дмитрий',
              date: '05/09/2017',
              stars: 5,
              review: `Работают на высшем уровне ребята, отлично знают свое дело.
                Заказывал котел здесь в прошлом сезоне, поставили и запустили оперативно.
                Теперь в этом году буду вызывать для обслуживания!`
            },
            { author: 'Евгений',
              date: '29/10/2017',
              stars: 4,
              review: `Сломался котел. позвонил сюда, приехали мастера и все сделали. спасибо!`
            },
            { author: 'Богдан Юрьевич',
              date: '30/11/2017',
              stars: 5,
              review: `Cпасибо за ремонт котла! Грамотный и порядочный мастер. Рекомендую!`
            },
            { author: 'Алина',
              date: '11/12/2017',
              stars: 5,
              review: `Спасибо за понимание и быстрое решение нашей проблемы с Аристоном! Без вас мы бы замерзли =)`
            },
            { author: 'Михаил',
              date: '23/01/2018',
              stars: 5,
              review: `Сначало вызвал "мастера" по ремонту болера с олх, в итоге тот так бойлер не починил,
                      только за вызов взял. Мастера этой же компании все сделали. Больше олх не доверяю, а вот
                      мастеров этой компании советую. Спасибо мастеру! Мои рекомендации 5/5`
            },
            { author: 'Тарас',
              date: '03/02/2018',
              stars: 4,
              review: `Качественная и аккуратная чистка за сравнительно невысокую цену, советую`
            },
            { author: 'Юлія',
              date: '29/03/2018',
              stars: 5,
              review: `Майстер приїхав на наступний день після виклику, потрібна була чистка бойлера. Дуже спокійно,
                        без зайвих коментарів, як це часто буває в наш час, зробив роботу, зауважу що дуже охайно!
                        Ретельно все перевірив на протікання. Раджу ВАМ і з задоволенням пораджу знайомим!`
            }
          ]
        },
        { title:'Контакты',
          active:false,
          id:5,
          header:'Звоните нам прямо сейчас!',
          sentence:`И мы гарантированно проведем качественные работы по обслуживанию, ремонту, и предоставим
                    всю необходимую информацию о интересующих Вас товарах.`,
          contactImg:contactImg,
          contacts:[
            { name:'Вадим',
              phones:[
                { number: '0562 33 72 79' },
                { number: '056 785 90 86' },
                {
                  number: '068 402 08 21',
                  logo: kyivstar,
                  title:'kyivstar'
                },
                {
                  number: '099 365 88 71',
                  logo: vodafone,
                  title: 'vodafone'
                },
                {
                  number: '073 132 23 34',
                  logo: life,
                  title: 'life'
                },
                {
                  number: '068 402 08 21',
                  logo: viber,
                  title: 'viber'
                },
                {
                  number: '067 632 72 00',
                  logo: kyivstar,
                  title: 'kyivstar'
                }
              ]
            }
          ]
        }
      ],
      sideToggler: {
        width:34,
        height:25
      },
      sideDrawerOpen:false,
      sectionsElem:[],
      windowDimensions:{
        width:0,
        height:0
      },
      currentSectionIndex:0
    }
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    this.debounceHandler = debounce(this.debounceHandler, 150);
  }

    componentDidMount = () => {
      this.updateWindowDimensions();
      window.addEventListener('wheel', this.onWheelHandler);
      window.addEventListener('resize', this.updateWindowDimensions);
    }

    componentWillUnmount = () => {
        window.removeEventListener('resize', this.updateWindowDimensions);
        window.removeEventListener('wheel', this.onWheelHandler);
    }

    debounceHandler = (deltaY, currentIndex, sectionsSum) => {
        if (deltaY > 0 && currentIndex < sectionsSum){
          this.setState({ currentSectionIndex:currentIndex+1 });
          this.scrollToComponentHandler(this.state.currentSectionIndex);
        }
        else if (deltaY < 0 && currentIndex > 0){
          this.setState({ currentSectionIndex:currentIndex-1 });
          this.scrollToComponentHandler(this.state.currentSectionIndex);
        }
    }

    onWheelHandler = (e) => {
      let currentIndex = this.state.currentSectionIndex,
          sectionsSum = this.state.navigationItems.length-1;
          this.debounceHandler(e.deltaY, currentIndex, sectionsSum);
    }

    updateWindowDimensions() {
      this.setState({ windowDimensions: {
                                          width:window.innerWidth,
                                          height: window.innerHeight
                                        }
                    });
    }

    updatedNavItemsHandler(index){
      let updatedNavItems = [...this.state.navigationItems];
          updatedNavItems.map(navItem => {
                navItem.active=false;
                return navItem;
          })
          updatedNavItems[index].active=true;
          updatedNavItems[index].img ? (updatedNavItems[index].textColor='#fff') : (updatedNavItems[index].textColor='#000')
          this.setState({ navigationItems:updatedNavItems,
                          sideDrawerOpen:false
                        });
    }

    toggleNavigationItemHandler = (index) => {
          this.setState({currentSectionIndex:index});
          this.scrollToComponentHandler(index);
    }

    scrollToComponentHandler(index) {
      this.updatedNavItemsHandler(index);
      scrollToComponent(this.state.sectionsElem[index],{offset:0,align:'top',duration:600,ease:'linear'});
    }

    sideDrawerClickHandler = () => {
      this.setState({sideDrawerOpen:!this.state.sideDrawerOpen})
    }

    getSectionsElemHandler = (section) => {
      this.state.sectionsElem.push(section);
    }

    closeSideDrawerHandler = () => {
      this.setState({sideDrawerOpen:false});
    }
    render () {
      return (
        <Adj>
          <Toolbar
            navigationItems={this.state.navigationItems}
            toggleNavigationItem={this.toggleNavigationItemHandler}
            sideDrawerOpen={this.state.sideDrawerOpen}
            windowDimensions={this.state.windowDimensions}
            currentSectionIndex={this.state.currentSectionIndex}
          />
          <SideToggler
            dimensions={this.state.sideToggler}
            toggleNavigationItem={this.toggleNavigationItemHandler}
            navigationItems={this.state.navigationItems}/>
          <SectionItems
            getSectionsElem={this.getSectionsElemHandler}
            navigationItems={this.state.navigationItems}
            windowDimensions={this.state.windowDimensions}
            toggleNavigationItem={this.toggleNavigationItemHandler}/>
          <SideDrawer
            closeSideDrawer={this.closeSideDrawerHandler}
            windowDimensions={this.state.windowDimensions}
            sideDrawerOpen={this.state.sideDrawerOpen}
            toggleNavigationItem={this.toggleNavigationItemHandler}
            navigationItems={this.state.navigationItems}/>
          <SideDrawerToggle
            sideDrawerOpen={this.state.sideDrawerOpen}
            sideDrawerClick={this.sideDrawerClickHandler}/>
        </Adj>
      )
    }
}

export default Layout;



